import React, { Component } from "react";
import { BrowserRouter as Router, HashRouter, Route, Link, Redirect } from "react-router-dom";
import { Menu } from "./partials/MenuHome";
import { Header } from "./partials/Header";
import { Home } from "./components/Home";
import { Bio } from "./components/Bio";
import { Video } from "./components/Video";
import { GraphicDesign } from "./components/GraphicDesign";
import { Dev } from "./components/Dev";
import { GlobalProvider } from "./context/GlobalContext";

class App extends Component {
  render() {
    return (
      <GlobalProvider>
        <HashRouter basename='/'>
          <div className="l-wrap" id="main">
            <div className="l-main">
              <Header />
              <Menu />
              <Route path="/bio" component={Bio} />
              <Route path="/video" component={Video} />
              <Route path="/graphic_design" component={GraphicDesign} />
              <Route path="/web_development" component={Dev} />
              <Route path="/home" component={Home} />
              <Route exact path="/" render={() => <Redirect to="/home" />} />
            </div>
          </div>
        </HashRouter>
      </GlobalProvider>
    );
  }
}

export default App;
